import React, { useState, useEffect } from "react";
import { Stage, Layer, Circle, Text, Line, Group, Rect } from "react-konva";

const Clock = () => {
  // Armazena as tarefas com chaves representando as horas (formato 24h)
  const [tasks, setTasks] = useState({});
  const [currentTime, setCurrentTime] = useState(new Date());
  const [selectedHour, setSelectedHour] = useState(null);
  const [showTaskForm, setShowTaskForm] = useState(false);
  const [newTask, setNewTask] = useState("");
  // Toggle para definir os rótulos do relógio: true exibe AM (1–12 com 12 no topo) e false exibe PM (13–24 com 24 no topo)
  const [isAMMode, setIsAMMode] = useState(() => new Date().getHours() < 12);

  const width = 600;
  const height = 600;
  const clockRadius = 150;
  const centerX = width / 2;
  const centerY = height / 2;

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);
    return () => clearInterval(timer);
  }, []);

  // Alterna os rótulos do relógio entre AM e PM
  const toggleClockMode = () => {
    setIsAMMode((prev) => !prev);
  };

  // Abre o modal para adicionar tarefa no horário clicado
  const handleAddTask = (hour) => {
    setSelectedHour(hour);
    setShowTaskForm(true);
  };

  const saveTask = () => {
    if (newTask.trim()) {
      setTasks((prev) => ({ ...prev, [selectedHour]: newTask }));
      setNewTask("");
      setShowTaskForm(false);
    }
  };

  const deleteTask = (hour) => {
    setTasks((prev) => {
      const newTasks = { ...prev };
      delete newTasks[hour];
      return newTasks;
    });
  };

  // Calcula a posição (x, y) com base num ângulo em graus e num raio
  const getPosition = (angleDegrees, radiusOffset) => {
    const angle = (angleDegrees * Math.PI) / 180;
    return {
      x: centerX + Math.cos(angle) * radiusOffset,
      y: centerY + Math.sin(angle) * radiusOffset,
    };
  };

  // Renderiza os botões e números do relógio
  // O mapeamento foi ajustado:
  // - Modo AM: índice 0 → 12, índice 1 → 1, ..., índice 11 → 11.
  // - Modo PM: índice 0 → 24, índice 1 → 13, ..., índice 11 → 23.
  const renderClockCircles = () => {
    return [...Array(12)].map((_, i) => {
      const hourLabel = isAMMode ? ((i + 11) % 12) + 1 : ((i + 11) % 12) + 13;
      const angle = i * 30 - 90; // distribui 12 botões de forma uniforme
      const numberPos = getPosition(angle, clockRadius - 20);
      const buttonPos = getPosition(angle, clockRadius - 20);
      return (
        <React.Fragment key={hourLabel}>
          <Text
            x={numberPos.x - 10}
            y={numberPos.y - 10}
            text={hourLabel.toString()}
            fontSize={isAMMode ? 16 : 16}
            fill={isAMMode ? "#000" : "#666"}
          />
          <Circle
            x={buttonPos.x}
            y={buttonPos.y}
            radius={15}
            fill="#e0e0e0"
            stroke="#999"
            onClick={() => handleAddTask(hourLabel)}
            opacity={0} // Torna o botão invisível, mas ainda interativo
          />
        </React.Fragment>
      );
    });
  };

  // Retorna a posição para exibir o rótulo da tarefa, usando o mesmo mapeamento dos botões
  const getTaskPosition = (hour) => {
    let index;
    if (hour >= 1 && hour <= 12) {
      // Para AM: se hour for 12, index = 0; caso contrário, index = hour
      index = hour === 12 ? 0 : hour;
    } else {
      // Para PM: se hour for 24, index = 0; caso contrário, index = hour - 12
      index = hour === 24 ? 0 : hour - 12;
    }
    return getPosition(index * 30 - 90, clockRadius + 60);
  };

  // Cálculo dos ponteiros do relógio (o cálculo do ponteiro das horas usa o módulo para 12h)
  const hours = currentTime.getHours();
  const minutes = currentTime.getMinutes();
  const seconds = currentTime.getSeconds();
  const hourAngle = (((hours % 12) * 30 + minutes / 2) - 90) * (Math.PI / 180);
  const minuteAngle = ((minutes * 6 + seconds / 10) - 90) * (Math.PI / 180);
  const secondAngle = ((seconds * 6) - 90) * (Math.PI / 180);

  // Separa as tarefas para exibição:
  // - À esquerda: tarefas AM (1–12)
  // - À direita: tarefas PM (13–24)
  const amTasks = Object.entries(tasks)
    .filter(([h]) => {
      const hr = parseInt(h, 10);
      return hr >= 1 && hr <= 12;
    })
    .sort(([a], [b]) => parseInt(a) - parseInt(b));
  const pmTasks = Object.entries(tasks)
    .filter(([h]) => {
      const hr = parseInt(h, 10);
      return hr >= 13 && hr <= 24;
    })
    .sort(([a], [b]) => parseInt(a) - parseInt(b));

  return (
    <div className="clock-container">

      <div style={{ display: "flex", justifyContent: "center", gap: "20px" }}>
    {/* Coluna da esquerda - Agenda AM */}
    <div style={{ 
      display: "flex",
      flexDirection: "column",
      alignItems: "center", // Centraliza horizontalmente
      padding: "10px"
    }}>
      <h3>00:00 / 12:00</h3>
      <div style={{ width: "100%" }}>
        {amTasks.map(([h, t]) => (
          <div key={h} style={{ margin: "5px", padding: "5px", background: "#f0f0f0" }}>
            {`${h.toString().padStart(2, "0")}:00 - ${t}`}
            <button onClick={() => deleteTask(h)} style={{ marginLeft: "10px" }}>×</button>
          </div>
        ))}
      </div>
    </div>

        {/* Relógio central */}
        <Stage width={width} height={height + 70}>
          <Layer>
            <Circle x={centerX} y={centerY} radius={clockRadius} fill="#fff" stroke="#000" strokeWidth={2} />
            {renderClockCircles()}

            {/* Ponteiros */}
            <Line
              points={[
                centerX,
                centerY,
                centerX + Math.cos(hourAngle) * (clockRadius - 60),
                centerY + Math.sin(hourAngle) * (clockRadius - 60),
              ]}
              stroke="#000"
              strokeWidth={4}
              lineCap="round"
            />
            <Line
              points={[
                centerX,
                centerY,
                centerX + Math.cos(minuteAngle) * (clockRadius - 30),
                centerY + Math.sin(minuteAngle) * (clockRadius - 30),
              ]}
              stroke="#00f"
              strokeWidth={3}
              lineCap="round"
            />
            <Line
              points={[
                centerX,
                centerY,
                centerX + Math.cos(secondAngle) * (clockRadius - 20),
                centerY + Math.sin(secondAngle) * (clockRadius - 20),
              ]}
              stroke="#f00"
              strokeWidth={1}
              lineCap="round"
            />
            <Circle x={centerX} y={centerY} radius={5} fill="#000" />
{/* Toggle starts here */}
<Group
  x={width / 2 - 25}
  y={height - 40}
  onClick={toggleClockMode}
>
  <Rect
    width={50}
    height={30}
    cornerRadius={15}
    fill={isAMMode ? "#34C759" : "#e0e0e0"}
  />
  <Circle
    x={isAMMode ? 18 : 32}
    y={15}
    radius={12}
    fill="#ffffff"
    shadowEnabled
    shadowColor="rgba(0,0,0,0.1)"
    shadowBlur={2}
  />
</Group>
{/* Toggle ends here */}
            {/* Exibição das tarefas sobre o relógio */}
            {Object.entries(tasks).map(([h, task]) => {
              const hr = parseInt(h, 10);
              if ((hr >= 1 && hr <= 12) || (hr >= 13 && hr <= 24)) {
                const pos = getTaskPosition(hr);
                return (
                  <Group key={h}>
                    <Line points={[centerX, centerY, pos.x, pos.y]} stroke="#4CAF50" strokeWidth={1} />
                    <Rect
                      x={pos.x - 60}
                      y={pos.y - 15}
                      width={120}
                      height={30}
                      fill="#fff"
                      stroke="#4CAF50"
                      cornerRadius={5}
                    />
                    <Text
                      x={pos.x - 55}
                      y={pos.y - 10}
                      text={`${h.toString().padStart(2, "0")}:00 - ${task}`}
                      fontSize={12}
                      width={110}
                      ellipsis
                    />
                  </Group>
                );
              }
              return null;
            })}
          </Layer>
        </Stage>

         {/* Coluna da direita - Agenda PM */}
    <div style={{ 
      display: "flex",
      flexDirection: "column",
      alignItems: "center", // Centraliza horizontalmente
      padding: "10px"
    }}>
      <h3>12:00 / 24:00</h3>
      <div style={{ width: "100%" }}>
        {pmTasks.map(([h, t]) => (
          <div key={h} style={{ margin: "5px", padding: "5px", background: "#f0f0f0" }}>
            {`${h.toString().padStart(2, "0")}:00 - ${t}`}
            <button onClick={() => deleteTask(h)} style={{ marginLeft: "10px" }}>×</button>
          </div>
        ))}
      </div>
    </div>
  </div>

      {/* Modal para adicionar tarefa */}
      {showTaskForm && (
        <div
          style={{
            position: "fixed",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            background: "white",
            padding: "20px",
            borderRadius: "8px",
            boxShadow: "0 2px 10px rgba(0,0,0,0.2)",
            zIndex: 1000,
          }}
        >
          <h3>{`Adicionar tarefa para ${selectedHour.toString().padStart(2, "0")}:00`}</h3>
          <input
            type="text"
            value={newTask}
            onChange={(e) => setNewTask(e.target.value)}
            placeholder="Descrição da tarefa"
            style={{ width: "90%", padding: "8px", margin: "10px 0" }}
          />
          <div style={{ display: "flex", gap: "10px", justifyContent: "flex-end" }}>
            <button onClick={() => setShowTaskForm(false)} style={{ padding: "8px 16px" }}>
              Cancelar
            </button>
            <button onClick={saveTask} style={{ padding: "8px 16px", background: "#4CAF50", color: "white" }}>
              Salvar
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Clock;
